import React from 'react';
import styled from 'styled-components';
import { go } from 'connected-react-router';
import { Input } from 'components/Input/Input';
import { useDispatch } from 'hooks/useDispatch';
import { useKeyboardExpanded } from 'hooks/useKeyboardExpanded';
import { useSelector } from 'hooks/useSelector';
import { addressChangeInputValueAction } from 'store/actions/addressSimplePage';

const StyledRoot = styled.div`
    margin: 80px 0 0 544px;
    width: 900px;
`;

export const AddressSimpleInput = () => {
    const dispatch = useDispatch();
    const inputValue = useSelector(({ addressSimplePage }) => addressSimplePage.inputValue);
    const [stateInputValue] = React.useState(inputValue);
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const [_, keyboardExpandedCount] = useKeyboardExpanded();
    const isInputValueChanged = inputValue !== stateInputValue;

    React.useEffect(() => {
        // ловим момент второго ресайза экрана, а именно, закрытия клавиатуры
        if (keyboardExpandedCount <= 1) {
            return;
        }

        dispatch(go(isInputValueChanged ? -2 : -1));
    }, [dispatch, keyboardExpandedCount, isInputValueChanged]);

    const handleInputChange = React.useCallback(
        (e: React.ChangeEvent<HTMLInputElement>) => {
            dispatch(addressChangeInputValueAction(e.target.value));
        },
        [dispatch],
    );

    return (
        <StyledRoot>
            <Input shouldFocusInputOnMount title="Адрес" value={inputValue} onChange={handleInputChange} />
        </StyledRoot>
    );
};
