import { Spinner } from 'components/Spinner/Spinner';
import { useAddressSimpleUpdateSuggests } from 'pages/AddressSimple/AddressSimple.hooks';
import React from 'react';
import { addressInputDeclineThunk } from 'store/thunks/native';
import styled from 'styled-components';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { addressSubmitAction } from 'store/actions/server';
import { HeadlineText1 } from 'components/Text/Headline';
import { Button, ButtonViews } from 'components/Button/Button';
import { color } from 'style/const/color';

const StyledAnswerContainer = styled.div`
    display: flex;
    flex-direction: column;
`;

const StyledAnswerTitle = styled(HeadlineText1)`
    margin-top: 34px;
    color: ${color.text.primary};
    text-align: center;
`;

const StyledSource = styled(HeadlineText1)`
    position: relative;
    margin-top: 32px;

    background: linear-gradient(to right, #00ffdf, #00ff5a);
    color: transparent;
    -webkit-background-clip: text;

    text-align: center;
`;

const StyledContainer = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
`;

const StyledAnswerButtonContainer = styled.div`
    display: flex;
    margin-top: 34px;
    justify-content: center;
`;

const StyledButton = styled(Button)`
    flex-grow: 1;
    max-width: 200px;
`;

const StyledButtonDivider = styled.div`
    width: 34px;
`;

export const AddressSimpleQuestion: React.FC = () => {
    const dispatch = useDispatch();
    const hasSuggests = useSelector(({ twoGisSuggestsData }) =>
        Boolean(Object.keys(twoGisSuggestsData.itemsMap).length),
    );
    const suggest = useSelector(({ twoGisSuggestsData: { mostRepresentationSuggest } }) => mostRepresentationSuggest);
    const handleAcceptButtonClick = React.useCallback(() => {
        dispatch(addressSubmitAction());
    }, [dispatch]);
    const handleDeclineButtonClick = React.useCallback(() => {
        dispatch(addressInputDeclineThunk());
    }, [dispatch]);

    useAddressSimpleUpdateSuggests();

    // саджесты еще не приехали, показываем спиннер
    if (!hasSuggests) {
        return (
            <StyledContainer>
                <Spinner />
            </StyledContainer>
        );
    }

    return (
        <StyledContainer>
            {suggest ? (
                <StyledAnswerContainer>
                    <StyledSource>{suggest.full_name}</StyledSource>
                    <StyledAnswerTitle>Это ваш адрес?</StyledAnswerTitle>
                    <StyledAnswerButtonContainer>
                        <StyledButton shouldFocusOnMount view={ButtonViews.secondary} onClick={handleAcceptButtonClick}>
                            Да
                        </StyledButton>
                        <StyledButtonDivider />
                        <StyledButton view={ButtonViews.secondary} onClick={handleDeclineButtonClick}>
                            Нет
                        </StyledButton>
                    </StyledAnswerButtonContainer>
                </StyledAnswerContainer>
            ) : (
                <StyledContainer>
                    <StyledAnswerTitle>Не получается найти такой адрес :(</StyledAnswerTitle>
                </StyledContainer>
            )}
        </StyledContainer>
    );
};
